/**
 * WP Epeex Related Style
 */
$color-primary: #1260aa;
$color-secondary: #51a8de;

body.toplevel_page_clevernode-settings {
    *,
    *::after,
    *::before {
        box-sizing: border-box;
    }

    .wrap {
        padding: 20px;
    }

    .checkbox-switch {
        fieldset input[type='checkbox'] {
            position: absolute;
            height: 0;
            width: 0;
            visibility: hidden;
        }

        fieldset label {
            cursor: pointer;
            text-indent: -9999px;
            width: 50px;
            height: 30px;
            background: grey;
            display: block;
            border-radius: 50px;
            position: relative;
        }

        fieldset label:after {
            content: '';
            position: absolute;
            top: 5px;
            left: 5px;
            width: 20px;
            height: 20px;
            background: #fff;
            border-radius: 20px;
            transition: 0.3s;
        }

        fieldset input:checked + label {
            background: $color-secondary;
        }

        fieldset input:checked + label:after {
            left: calc(100% - 5px);
            transform: translateX(-100%);
        }

        fieldset label:active:after {
            width: 30px;
        }
    }

    .metabox-holder {
        h2 {
            font-size: 1.5em;
            padding: 12px 0;
        }
    }

    .wp-media-upl:focus,
    .wp-media-upl:active {
        box-shadow: none;
    }

    .clevernode-notice {
        display: block !important;
    }

    .clevernode-settings {
        h1 {
            padding: 4rem 0 1rem;
            margin-bottom: 1rem;
            background: url('../img/logo_clevernode.svg') no-repeat top left;
        }

        @media (min-width: 900px) {
            h1 {
                padding: 1rem 0;
                background-position: center right;
            }
        }
    }

    .support-content {
        @media (min-width: 900px) {
            max-width: 60%;
        }
    }

    .support-user-data {
        margin: 20px 0;

        h3 {
            font-size: 1rem;
            text-transform: uppercase;
            color: $color-secondary;
        }

        table.user-data {
            width: 600px;
            margin: 10px 0;
            border-spacing: 10px;
            border: 2px solid $color-secondary;
            background-color: $color-primary;
            text-align: left;
            color: #fff;

            td,
            th {
                padding: 10px;
            }

            th {
                padding-bottom: 0;
                text-transform: uppercase;
                font-size: 0.8rem;
                color: $color-secondary;
            }
        }
    }

    .connection-form {
        th {
            padding: 0;
        }

        td.connection-status p {
            margin-top: 0;
            display: inline;
            padding: 5px;
        }
    }

    .status-success {
        background-color: #d4edda;
        color: #155724;
    }

    .status-warning {
        background-color: #fff3cd;
        color: #856404;
    }

    // Banner
    .clevernode-banner {
        position: relative;
        background-color: #fefefe;
        padding: 1rem;
        margin-bottom: 20px;
        border: 1px solid #ddd;
        max-width: 900px;
        transition: all .4s;

        &.hide {
            margin: 0;
            padding: 0;
            opacity: 0;
            height: 0;
            overflow: hidden;
        }

        h2, h3 {
            margin: 0;
            line-height: 1.2;
        }
        
        h2 {
            font-size: 1.2rem;
            color: $color-primary;
            margin-bottom: 0.8rem;
        }
        
        h3 {
            font-size: 1.5rem;
            color: $color-secondary;
        }
        
        .banner-content {
            text-align: center;

            img {
                margin-bottom: 0.5rem;
            }

            @media(min-width: 781px) {
                display: flex;
                align-items: center;
                text-align: left;
                
                p { margin-bottom: 0; }

                img {
                    width: 100%;
                    height: auto;
                    margin-bottom: 0;
                }
                
                .col-1 {
                    flex: 0 0 10%;
                }

                .col-2 {
                    flex: 0 0 70%;
                    padding: 0 1rem;
                }
                
                .col-3 {
                    flex: 0 0 20%;
                    text-align: center;
                }
            }
        }

        #close-banner {
            position: absolute;
            top: 10px; right: 10px;
            text-decoration: none;
            font-weight: bold;
            color: #aaa;
        }
    }
    
    // Modal
    .clevernode-modal {
        position: fixed;
        bottom: 10px; right: 10px;
        width: 90%;
        max-width: 400px;
        transition: all .4s;
        animation: slideIn 0.5s forwards;
        
        &.hide {
            margin: 0;
            padding: 0;
            opacity: 0;
            overflow: hidden;
            animation: slideOut 0.8s forwards;
        }
        
        .modal-inner {
            background-color: lighten($color-secondary, 36%);
            border: 1px solid lighten($color-secondary, 25%);
            padding: 20px;
            opacity: 1;
            transition: all .4s;
        }

        .modal-content {
            h2 {
                color: $color-secondary;
                font-size: 20px;
            }

            h3 {
                color: $color-primary;
            }

            img {
                float: left;
                margin-right: 10px;
            }

            .heading {
                overflow: auto;
            }
        }

        #close-modal {
            position: absolute;
            top: 10px; right: 10px;
            text-decoration: none;
            font-weight: bold;
            color: lighten($color-secondary, 25%);
            font-size: 20px;
        }
    }
}

// Success button
.wp-core-ui .button-success {
    background-color: #42bc42;
    border-color: #32a132;
    color: #fff;

    &:hover,
    &:focus,
    &:active {
        background-color: #32a132;
        border-color: #258325;
        color: #fff;
    }
}


@keyframes slideIn {
    0% {
        right: -200%;
    }
    100% {
        right: 10px;
    }
}

@keyframes slideOut {
    0% {
        right: 10px;
    }
    100% {
        right: -200%;
    }
}